import { useState, useEffect } from "react";
import { Container, Row, Col } from "react-bootstrap";
import { ArrowRightCircle } from 'react-bootstrap-icons';
import 'animate.css';
import TrackVisibility from 'react-on-screen';

export const Banner = () => {
    const [loopNum, setLoopNum] = useState(0);
    const [isDeleting, setIsDeleting] = useState(false);
    const [text, setText] = useState('');
    const [delta, setDelta] = useState(200 - Math.random() * 100);
    const [index, setIndex] = useState(1);
    const toRotate = ["Student", "Software Developer", "Web Developer" ,"Mobile Developer"];
    const period = 650;
  
    useEffect(() => {
      let ticker = setInterval(() => {
        tick();
      }, delta);
  
      return () => { clearInterval(ticker) };
    }, [text])
  

    const tick = () => {
        let i = loopNum % toRotate.length;
        let fullText = toRotate[i];
        let updatedText = isDeleting ? fullText.substring(0, text.length - 1) : fullText.substring(0, text.length + 1);
    
        setText(updatedText);
    
        if (isDeleting) {
          setDelta(period);
        }
    
        if (!isDeleting && updatedText === fullText) {
          setIsDeleting(true);
          setIndex(prevIndex => prevIndex - 1);
          setDelta(period);
        } else if (isDeleting && updatedText === '') {
          setIsDeleting(false);
          setLoopNum(loopNum + 1);
          setIndex(1);
          setDelta(100);
        } else {
          setIndex(prevIndex => prevIndex + 1);
        }
      }
    


  return (
    <section className="banner" id="home">
      <Container>
        <Row className="aligh-items-center">
          <Col xs={12} md={6} xl={7}>
            <TrackVisibility>
              {({ isVisible }) =>
              <div className={isVisible ? "animate__animated animate__slide-right" : ""}>
                <span className="tagline">About Me</span>
                <h1>{`Hello there! I'm Zaid Ismail,I'm a`} <span className="txt-rotate"><span className="wrap">{text}</span></span></h1>
                  <p>I am a highly motivated and skilled student seeking a junior developer role. With a strong foundation in programming languages such as Java and C++, as well as proficiency in application development, configuration management, and database design, I am confident in my ability to make a meaningful contribution to your company. I am ambitious and excited to gain valuable industry experience, continuously learn and adapt to new technologies and methodologies, and work effectively in a team with strong problem-solving skills and excellent communication. I am confident that my skills and dedication make me a valuable asset to your team and am excited to join your company as a junior developer.</p>
                  <button onClick={() => console.log('connect')}>Lets Connect <ArrowRightCircle size={25} /></button>
              </div>}
            </TrackVisibility>
          </Col>
        </Row>
      </Container>
    </section>
  )
}
